@font-face {
  font-family: 'Poppins';
  src: url('./font/Poppins-Regular.ttf');
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins';
}
body{
  width: 100vw;
  height: 100vh;
  background: linear-gradient(to right, #8A2387, #E94057, #F27121);
  display: flex;
  justify-content: center;
  align-items: center;
}
